<template>
    <div class="distributor-main">
        <div class="list-top">
            <div class="right-font">
                <el-input :placeholder="$L('输入用户名称/姓名')" size="small" v-model="inputValue" class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
        </div>
        <div class="main-table" v-if="tableData && tableData.hasOwnProperty('list')">
            <el-table
                    v-loading="loading"
                    :empty-text="$L('暂无数据')"
                    :data="tableData.list"
                    :header-cell-style="{background:'#f0f1f5',padding:'10px',color:'#666'}"
                    style="width: 100%" class="table">
                <el-table-column
                        prop="grade"
                        :label="$L('等级')"
                        align="center">
                </el-table-column>
                <el-table-column
                        :label="$L('用户')"
                        align="center">
                    <div slot-scope="scope">
                        <p>{{ scope.row.contact ? scope.row.contact : scope.row.userName }}{{scope.row.mobile?'/'+scope.row.mobile:''}}</p>
                    </div>
                </el-table-column>
                <el-table-column
                        prop="subordinateCount"
                        :label="$L('下级分销商数')"
                        align="center">
                </el-table-column>
                <el-table-column
                        prop="memberCount"
                        :label="$L('子会员数')"
                        align="center">
                </el-table-column>
                <el-table-column
                        prop="totalAmount"
                        :label="$L('订单总金额')"
                        align="center">
                </el-table-column>
                <el-table-column
                        prop="fullCommission"
                        :label="$L('产生佣金总额')"
                        align="center">
                </el-table-column>
                <el-table-column
                        :label="$L('状态')"
                        align="center">
                    <div slot-scope="scope" class="status">
                        <span :class="statusFunc(scope.row.status).color">{{ statusFunc(scope.row.status).text }}</span>
                    </div>
                </el-table-column>
                <el-table-column
                        :label="$L('操作')"
                        min-width="120"
                        align="center">
                    <div slot-scope="scope" class="button">
                        <a class="btns" :href="scope.row.fxUrl" target="_blank">
                            <el-button size="small">{{ $L('进入平台') }}</el-button>
                        </a>
                        <router-link :to="{name:'distributorDetails',params:{id:scope.row.distributorId,type:'all'}}">
                            <el-button type="primary" size="small">{{ $L('查看详情') }}</el-button>
                        </router-link>
                    </div>
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <div class="list-pages" v-if="tableData.allNum > pageSize ">
                <el-pagination
                        background
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-size="pageSize"
                        layout="prev, pager, next, total, jumper"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "distributorList",
        props : {
            distributormain : {
                type : Boolean
            }
        },
        data () {
            return {
                loading:false,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                btnSize:'small',
                inputValue:'',
                type:'',
                cur:1,
                messageType:'all',
                theadName:[],
                messageTypelist:[
                    {
                        type:'all',
                        name:this.$L('全部状态')
                    },
                    {
                        type:'valid',
                        name:this.$L('生效')
                    },
                    {
                        type:'stop',
                        name:this.$L('停用')
                    },
                ],
                tableData: {},
                isMyMember:0,
                pageSize:10,
                currentPage:1
            }
        },
        beforeRouteUpdate (to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.getData(to);
            this.currentPage = 1;
            next();
        },
        created () {
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p:_self.currentPage,
                    keyword:_self.inputValue?_self.inputValue:"",
                };
                if(!router){
                    router = _self.$route;
                };
                _self.$api('distributionList', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.theadName = reData.data.group;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            searchFunc(val){
                this.inputValue = val;
                this.getData();
            },
            changeTypeFunc(val){
                this.messageType = val;
                this.getData();
            },
            statusFunc(value){
                let x;
                switch(value){
                    case -1:
                        x = {text:this.$L('停用'),color:'font-red'};
                        break;
                    default:
                        x = {text:this.$L('生效'),color:'font-green'};
                        break;
                }
                return x;
            },
        },

    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib";
    .distributor-main{
        position: relative;
        padding: 0 20px 20px !important;
        .list-top{
            position: relative;
            padding-top: 10px;
            height: 45px;
            .search{
                width: 120px;
                padding-left: 10px;
            }
            .right-font{
                position: absolute;
                right: 0;
                display: inline-block;
                .input-with-select{
                    width: 260px;
                    padding-left: 10px;
                    /deep/ .el-input-group__append{
                        font-size: 14px;
                        color: #333;
                        padding: 0 10px;
                        background: #fff;
                        border-left-color: #fff;
                    }
                }
            }
        }
        .main-table{
            margin-top: 10px;
            padding: 20px;
            background: #fff;
            /deep/ .el-table__body-wrapper{
                border-left: 1px solid #ebeef5;
                border-right: 1px solid #ebeef5;
            }
            /deep/ .el-table__header-wrapper .cell{
                font-weight: 800;
            }
            .table{
                .status{
                    .font-red {
                        color: #ff0000;
                    }
                    .font-green {
                        color: #00bf00;
                    }
                }
                .name{
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                .button{
                    .btns{
                        margin-right: 10px;
                    }
                    .btns:last-child{
                        margin-right: 0;
                    }

                }
            }
            .list-pages{
                text-align: center;
                padding: 20px 0 0;
                background: #fff;
            }
        }
        .list-no-data {
			margin-top: 10px;
            @extend .no-select;
            position: relative;
            width: 100%;
            height: 380px;
            line-height: 380px;
            text-align: center;
            background-color: #ffffff;
            font-size: 16px;
            color: #999;
            margin-bottom: 20px;
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>